<template>
    <div>
        <el-main>
            <div class="btn">
                <el-button type="danger" icon="el-icon-delete-solid">批量删除</el-button>
                <el-button type="primary" icon="el-icon-plus">添加</el-button>
                <el-button type="primary" icon="el-icon-document">导出</el-button>
            </div>
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'wid', order: 'descending'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="wid" label="工号" width="120" sortable></el-table-column>
                <el-table-column prop="name" label="姓名" width="120"></el-table-column>
                <el-table-column
                    prop="sex"
                    label="性别"
                    width="100"
                    :filters="[{ text: '男', value: '男' }, { text: '女', value: '女' }]"
                    :filter-method="filterSex"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.sex === '男' ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.sex}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="department" label="部门" width="120"></el-table-column>
                <el-table-column prop="phone" label="电话号码" width="120"></el-table-column>
                <el-table-column prop="identity" label="身份证号码" show-overflow-tooltip></el-table-column>
                <el-table-column
                    prop="state"
                    label="状态"
                    width="100"
                    :filters="[{ text: '在职', value: '在职' }, { text: '离职', value: '离职' }]"
                    :filter-method="filterState"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-popover
                            placement="top-start"
                            width="150"
                            trigger="hover"
                            content="签约时间：2019-01-01   离职时间：--"
                        >
                            <el-button slot="reference">
                                <el-tag
                                    :type="scope.row.state === '在职' ? 'success' : 'danger'"
                                    disable-transitions
                                >{{scope.row.state}}</el-tag>
                            </el-button>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column align="right">
                    <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入姓名搜索" />
                    </template>
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleDetail(scope.$index, scope.row)">详细</el-button>
                        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
        <detail
            :detailtem="detailitem"
            :detailVisible="detailVisible"
            @close-dialogStatus="Close_dialog"
        ></detail>
    </div>
</template>
<style lang="stylus" scoped>
body {
    margin: 0;
}

.bottom {
    margin-top: 13px;
    line-height: 12px;
}

.button {
    padding: 0;
    float: right;
}

.image {
    width: 100%;
    height: 100%;
    display: block;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both;
}
.btn {
    margin-bottom: 10px;
}
</style>

<script>
import detail from "@/components/detail";
export default {
    data() {
        return {
            search: "",
            detailitem: {},
            detailVisible: false,

            tableData: [
                {
                    wid: "2019500",
                    name: "李狗蛋",
                    sex: "男",
                    department: "国防部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "在职"
                },
                {
                    wid: "2019501",
                    name: "张全蛋",
                    sex: "女",
                    department: "教育部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "在职"
                },
                {
                    wid: "2019502",
                    name: "李狗嗨",
                    sex: "男",
                    department: "外交部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "在职"
                },
                {
                    wid: "2019503",
                    name: "王二狗",
                    sex: "女",
                    department: "国防部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "离职"
                },
                {
                    wid: "2019504",
                    name: "赵铁柱",
                    sex: "男",
                    department: "国防部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "在职"
                },
                {
                    wid: "2019505",
                    name: "狗剩子",
                    sex: "男",
                    department: "外交部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "离职"
                },
                {
                    wid: "2019506",
                    name: "李大拐",
                    sex: "男",
                    department: "民政部",
                    phone: "110",
                    identity: "12345678910111213",
                    state: "在职"
                }
            ],
            multipleSelection: []
        };
    },
    created: function() {},
    mounted: function() {},
    components: {
        detail
    },
    methods: {
        Close_dialog(val) {
            this.detailVisible = false;
        },
        show() {
            this.detailitem = 1;
            this.detailVisible = true;
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        filterSex(value, row) {
            return row.sex === value;
        },
        filterState(value, row) {
            return row.state === value;
        },
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
        },
        handleDetail(index, row) {
            console.log(index, row);
        }
    }
};
</script>



