<template>
    <div>
        <el-dialog title="详细信息" :visible.sync="dialogTableVisible" @close="closeDialog">
            <el-form
                :model="ruleForm"
                status-icon
                :rules="rules"
                ref="ruleForm"
                label-width="100px"
            >
                <el-row>
                    <el-col :span="4">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload"
                        >
                            <img v-if="imageUrl" :src="imageUrl" class="avatar"/>
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-col>
                    <el-col :span="20">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="姓名" prop="name">
                                    <el-input v-model="ruleForm.name" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="性别" prop="sex">
                                    <el-input v-model="ruleForm.sex" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="学历" prop="degree">
                                    <el-input v-model="ruleForm.degree" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="身份证号" prop="identity">
                                    <el-input v-model="ruleForm.identity" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="部门" prop="department">
                                    <el-input v-model="ruleForm.department" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="工号" prop="wid">
                                    <el-input v-model="ruleForm.wid" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="签约时间" prop="signingTime">
                                    <el-input v-model="ruleForm.signingTime" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="离校时间" prop="departureTime">
                                    <el-input v-model="ruleForm.departureTime" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="电话号码" prop="phone">
                                    <el-input v-model="ruleForm.phone" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
                <el-row>
                    <el-form-item style="text-align: center;">
                        <el-button type="primary" @click="submitForm('ruleForm')">修改</el-button>
                        <el-button @click="dialogTableVisible=false;">取消</el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            imageUrl: "https://bulaya.github.io/static/img/teacherLi.82ac0df.jpg",
            dialogTableVisible: false,
            ruleForm: {
                name: "李老师",
                sex: "男",
                identity: "110",
                department: "外籍教师",
                phone: "120",
                wid: "001",
                signingTime: "2010",
                departureTime: "2019",
                degree: "博士"
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                sex: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                identity: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                department: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                phone: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                wid: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                signingTime: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                departureTime: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ],
                degree: [
                    {
                        required: true,
                        message: "请输入邮箱地址",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    props: {
        detailVisible: Boolean,
        detailitem: Object
    },
    watch: {
        detailVisible(newValue, oldValue) {
            this.dialogTableVisible = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        submitForm(ruleForm) {
            this.dialogTableVisible = false;
            this.$message("修改成功");
        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isLt2M) {
                this.$message.error("上传头像图片大小不能超过 2MB!");
            }
            
        }
    }
};
</script>

<style lang="stylus" scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409eff;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}
.avatar {
    width: 137px;
    height: 169px;
    display: block;
}
</style>


